import { useState, useEffect } from "react";
import { styled } from '@mui/system';
import { MatxVerticalNav } from 'app/components';
import useSettings from 'app/hooks/useSettings';
import { navigations } from 'app/navigations';
import { sellerNavigations } from 'app/sellerNavigations';
import { retailerNavigations } from 'app/retailerNavigations';
import { Fragment } from 'react';
import Scrollbar from 'react-perfect-scrollbar';

const StyledScrollBar = styled(Scrollbar)(() => ({
  paddingLeft: '1rem',
  paddingRight: '1rem',
  position: 'relative',
 
}));

const SideNavMobile = styled('div')(({ theme }) => ({
  position: 'fixed',
  top: 0,
  left: 0,
  bottom: 0,
  right: 0,
  width: '100vw',
  background: 'rgba(0, 0, 0, 0.54)',
  zIndex: -1,
  [theme.breakpoints.up('lg')]: { display: 'none' },
}));

const Sidenav = ({ children }) => {
  const register_as =localStorage.getItem('register_as');
  const { settings, updateSettings } = useSettings();
  const updateSidebarMode = (sidebarSettings) => {
    let activeLayoutSettingsName = settings.activeLayout + 'Settings';
    let activeLayoutSettings = settings[activeLayoutSettingsName];

    updateSettings({
      ...settings,
      [activeLayoutSettingsName]: {
        ...activeLayoutSettings,
        leftSidebar: {
          ...activeLayoutSettings.leftSidebar,
          ...sidebarSettings,
        },
      },
    });
  };

  return (
    <Fragment>
      <StyledScrollBar options={{ suppressScrollX: true }}>
        {children}
        {(() => {
          switch (register_as) {
            case 'M':
              return <MatxVerticalNav items={navigations} />
            case 'D':
              return <MatxVerticalNav items={sellerNavigations} />
            case 'W':
              return <MatxVerticalNav items={sellerNavigations} />
            case 'rejected':
              return <MatxVerticalNav items={retailerNavigations} />
            default:
              return <MatxVerticalNav items={retailerNavigations} />
            }
        })()}
         
      </StyledScrollBar>
      <SideNavMobile onClick={() => updateSidebarMode({ mode: 'close' })} />
    </Fragment>
  );
};

export default Sidenav;
